import axios from 'axios';
import React, { Component } from 'react';

export default class Register extends Component {

  user = {
    first_name: '',
    last_name: '',
    email: '',
    password: ''
  }

  getFormDate= (e)=>{
    this.user[e.target.name] = e.target.value
    // console.log(this.user);
  }

  sendData = async (e) =>{
    e.preventDefault(); 
    let {data} = await axios.post(`https://route-egypt-api.herokuapp.com/signup`, this.user)
    if(data.message==="success"){
      e.target.reset()
      this.props.history.replace('/login')
    }else{
      this.props.history.replace('/')
    }
    // console.log(data);
    
  }

  render() {
    return (
      <>
        <form onSubmit={this.sendData} className="form w-75 mx-auto my-5">
            <input type="text" onChange={this.getFormDate} name="first_name" placeholder="First Name" className="form-control my-3"/>
            <input type="text" onChange={this.getFormDate} name="last_name" placeholder="last Name" className="form-control my-3"/>
            <input type="email " onChange={this.getFormDate} name="email" placeholder="email " className="form-control my-3"/>
            <input type="password" onChange={this.getFormDate} name="password" placeholder="password" className="form-control my-3"/>
            <button className="btn btn-primary float-right">Register</button>
        </form>
      </>
    );
  }
}
