import React, { Component } from 'react';
import axios from 'axios';

export default class Home extends Component {

  state = {movies : [], tv : []}

  getTrending = async (mediaType)=>{
    let {data} = await axios.get(`https://api.themoviedb.org/3/trending/${mediaType}/day?api_key=52bbcddeda849047525b51d6f8a12361`)
    // console.log(data.results);
    this.setState({
     [mediaType] : data.results 
    })
  }

  componentDidMount(){
    this.getTrending('movies')
    this.getTrending('tv')
  }
  render() {
    return (
      <>
        <div className="container my-5">
          <div className="row">
            <div className=" col-md-4 ">
              <div className="brdr w-25 my-3"></div>
              <h1>Trending <br/>Movies<br/> To Watch Now</h1>
              <p className="secondFontColor"> Most Watched Movies By Days</p>
              <div className="brdr w-100 my-3"></div>
            </div>
            {this.state.movies.map((value,index)=>{
            return (<div key={index} className="col-lg-2 col-md-4 ">
              <div className="item my-4">
                <img className="w-100" src={'https://image.tmdb.org/t/p/original' + value.poster_path} alt=""/>
                <h5>{value.name} {value.title}</h5>
                <div className="vote">{value.vote_average}</div>
              </div>
            </div>
            )}).slice(0,10)}
          </div>
          <div className="row">
            <div className=" col-md-4 ">
              <div className="brdr w-25 my-3"></div>
              <h1>Trending <br/>Tv shows<br/> To Watch Now</h1>
              <p className="secondFontColor"> Most Watched Tv shows By Days</p>
              <div className="brdr w-100 my-3"></div>
            </div>
            {this.state.tv.map((value,index)=>{
            return (<div key={index} className="col-lg-2 col-md-4 ">
              <div className="item my-4">
                <img className="w-100" src={'https://image.tmdb.org/t/p/original' + value.poster_path} alt=""/>
                <h5>{value.name} {value.title}</h5>
                <div className="vote">{value.vote_average}</div>
              </div>
            </div>
            )}).slice(0,10)}
          </div>
        </div>
      </>
    );
  }
}
