import React, { Component } from 'react';
import axios from 'axios'
export default class People extends Component {
  
  state = {people : []}  

  getPeople = async ()=>{
    let {data} = await axios.get(`https://api.themoviedb.org/3/person/popular?api_key=52bbcddeda849047525b51d6f8a12361&language=en-US&page=1`)
    // console.log(data.results)
    this.setState({
        people : data.results
    })
  }

  componentDidMount(){
    this.getPeople()
  }

  render() {
    return (
      <>
      <div className="container my-5">
          <div className="row">
          <div className=" col-md-4 ">
              <div className="brdr w-25 my-3"></div>
              <h1>Popular <br/>Celebs<br/> Now</h1>
              <p className="secondFontColor"> Most poupular celebs By Days</p>
              <div className="brdr w-100 my-3"></div>
            </div>
                {this.state.people.map((value,index) => {
                    return(
                    <div key = {index} className="col-lg-4 col-md-4">
                        <div className="item my-4">
                            <img className="w-100" src={'https://image.tmdb.org/t/p/original' + value.profile_path} alt="" />
                            <h5>{value.name} {value.title}</h5>
                            <span >popularity: {value.popularity}</span>
                        </div>
                    </div>
                    )
                })}
              
          </div>
      </div>
      </>
    );
  }
}


