import React, { Component } from 'react';
import axios from 'axios'
export default class Login extends Component {
  state={errorMessage:""}
  user = {
    email: '',
    password: ''
  }

  getFormDate= (e)=>{
    this.user[e.target.name] = e.target.value

    // console.log(this.user);
  }

  sendData = async (e) =>{
    e.preventDefault(); 
    let {data} = await axios.post(`https://route-egypt-api.herokuapp.com/signin`, this.user)
    if(data.message==="success"){
      localStorage.setItem("token",data.token)
      //home 
      this.props.history.replace('/home')
    }else{
      this.setState({errorMessage:data.message})
    }
    // console.log(data);
    
  }
  render() {
    return (
      <> 
        <form onSubmit={this.sendData} className="form w-75 mx-auto my-5">
            <input type="email " onChange={this.getFormDate} name="email" placeholder="email " className="form-control my-3"/>
            <input type="password" onChange={this.getFormDate} name="password" placeholder="password" className="form-control "/>
            <div className="alert alert-transparent text-danger">{this.state.errorMessage}</div>
            <button className="btn btn-primary float-right">Login</button>
        </form>

      </>
    );
  }
}
