import React, { Component } from 'react';
import {NavLink} from 'react-router-dom'
export default class Navbar extends Component {
  render() {
    return (
      <>
        <nav className="navbar navbar-expand-lg navbar-dark ">
  <NavLink className="navbar-brand" to="/">Noxe</NavLink>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>

  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav mr-auto">
      
      <li className="nav-item">
        <NavLink className="nav-link" to="/home">Home</NavLink>
      </li>
      <li className="nav-item">
        <NavLink className="nav-link" to="/movies">Movies</NavLink>
      </li>
      <li className="nav-item">
        <NavLink className="nav-link" to="/tv">Tv shows</NavLink>
      </li>
      <li className="nav-item">
        <NavLink className="nav-link" to="/people">People</NavLink>
      </li>
    </ul>
    <form className="form-inline my-2 my-lg-0 ">
      <input className="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search" />
      <div className="icons ">
        <a href="Https://www.facebook.com" target="_blank"><i className="fab text-white mx-2 fa-facebook-f "></i></a>
        <a href="Https://www.spotify.com" target="_blank"><i className="fab text-white mx-2 fa-spotify "></i></a>
        <a href="Https://www.instagram.com" target="_blank"><i className="fab text-white mx-2 fa-instagram "></i></a>
        <a href="Https://www.youtube.com" target="_blank"><i className="fab text-white mx-2 fa-youtube "></i></a>
      </div>
      <button className="btn btn-transparent text-white ml-5 my-2 my-sm-0" type="submit">Logout</button>
    </form>
  </div>
</nav>
      </>
    );
  }
}
