import React, { Component } from 'react';
import axios from 'axios';
export default class Movies extends Component {

  state = {movies : []}  

  getMovies = async ()=>{
    let {data} = await axios.get(`https://api.themoviedb.org/3/trending/movies/day?api_key=52bbcddeda849047525b51d6f8a12361`)
    // console.log(data.results)
    this.setState({
        movies : data.results
    })
  }

  componentDidMount(){
    this.getMovies()
  }

  render() {
    return (
      <>
      <div className="container my-5">
          <div className="row">
          <div className=" col-md-4 ">
              <div className="brdr w-25 my-3"></div>
              <h1>Trending <br/>Movies<br/> To Watch Now</h1>
              <p className="secondFontColor"> Most Watched Movies By Days</p>
              <div className="brdr w-100 my-3"></div>
            </div>
                {this.state.movies.map((value,index) => {
                    return(
                    <div key = {index} className="col-lg-2 col-md-4">
                        <div className="item">
                            <img className="w-100" src={'https://image.tmdb.org/t/p/original' + value.poster_path} alt="" />
                            <h5>{value.name} {value.title}</h5>
                            <span className="vote">{value.vote_average}</span>
                        </div>
                    </div>
                    )
                })}
              
          </div>
      </div>
      </>
    );
  }
}
