import React, { Component } from 'react';
import img1 from "../../imgs/notfound.png"
export default class NotFound extends Component {
  render() {
    return (
        <div className="not-found bg-dark vh-100 w-100 d-flex justify-content-center align-items-center"> 
            <img src={img1} alt="" className="w-50" />
            <p >Error 404:<br/>The Page Requested not found</p>
        </div>
    );
  }
}
